}
.timeline-split .timeline span {
  color: #666;
  display: block;
  margin-bottom: 10px;
}
/*top circle */
.timeline-split .timeline::before {
  content: "";
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  background: #25cbd3;
  left: calc(50% - 10px);
}
/*vertival line */
.timeline-split .timeline::after {
  content: "";
  width: 2px;
  height: 4000px;
  display: block;
  position: absolute;
  top: 10px;
  left: 50%;
  left: calc(50% - 1px);
  background: #25cbd3;
}

/*timeline block */
.timeline-split .timeline .block {
  width: 50%;
  padding: 30px;
  background: #0d003b;
  border: 1px solid #25cbd3;
  width: calc(50% - 74px);
  text-align: left;
  position: relative;
