}
.timeline .block .between {
  padding: 5px 10px;
  display: inline-block;
  color: #fff;
}
/*block marker */
.timeline-split .timeline .block::before {
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 50%;
  position: absolute;
  background: #25cbd3;
  top: 30px;
}
.timeline-split .timeline .block.block-left::after,
.timeline-split .timeline .block.block-right::after {
  content: "";
  width: 79px;
  height: 2px;
  position: absolute;
  background: #25cbd3;
  top: 34px;
  z-index: 0;
  right: -78px;
}
.timeline-split .timeline .block.block-right::after {
  left: -80px;
}
