    margin-top: 5px !important;
  }

  .main-timeline .date-content4 {
    margin-top: 6px !important;
  }

  .main-timeline .date-content6 {
    margin-top: -19px !important;
  }
}

.main-timeline .date-content:before {
  content: "";
  width: 36.5%;
